export default function () {
  if ($('.page--brand-platform').length == 0) return

  const hero_image = '.brand-platform--hero--image'
  gsap.fromTo(hero_image,
    {
      yPercent: -30,
    },
    {
      yPercent: 0,
      scrollTrigger:
      {
        trigger: hero_image,
        start: "top bottom",
        end: "bottom -100%",
        scrub: 1,
      },
    }
  )



  const foreground = '.brand-platform--text-image--backdrop .foreground'
  gsap.fromTo(foreground,
    {
      width: '0%',
      left: '0%',
    },
    {
      width: '100%',
      scrollTrigger:
      {
        trigger: foreground,
        start: "top 75%",
        end: "top 0%",
        scrub: 1,
      },
    }
  )

  const foreground_invert = '.brand-platform--text-image--backdrop .foreground-invert'
  gsap.fromTo(foreground_invert,
    {
      width: '0%',
      right: '0%',
      left: '100%',
    },
    {
      width: '100%',
      left: '0%',
      scrollTrigger:
      {
        trigger: foreground_invert,
        start: "top 50%",
        end: "top -25%",
        scrub: 1,
      },
    }
  )
}