import ScrollTrigger from "gsap/ScrollTrigger"
import Easings from './easings'

//gsap.registerPlugin(ScrollTrigger)

const filters = $('.collection--filters')
const has_filters = filters.length > 0

// site header
export default function (delay = 0) {
  const header_timeline = gsap.timeline({
    paused: true,
  })
  setTimeout(() => {
    gsap
      .fromTo(
        '.header--left',
        { xPercent: -5, duration: 0 },
        { xPercent: 0, opacity: 1, duration: 0.75 }
      )
    gsap
      .fromTo(
        '.header--language,.header--search',
        { yPercent: -100, duration: 0 },
        { yPercent: 0, opacity: 1, duration: 1 }
      )
    gsap
      .fromTo(
        '.header--toggle',
        { xPercent: 100, duration: 0 },
        { xPercent: 0, opacity: 1, duration: 1 }
      )
      .play()

    const show_header = gsap.from('.header', {
      yPercent: -100,
      opacity: 0,
      paused: true,
      duration: 1,
      ease: Easings.header,
    }).progress(1);

    //let filters_height = $('.collection--filters').outerHeight()//,
    //header_height = $('.header').outerHeight()

    ScrollTrigger.create({
      start: "top top",
      end: 99999,
      onUpdate: (self) => {
        if (self.direction === -1) {
          if (!has_filters) {
            show_header.play()
          }
          else {
            if ($(window).scrollTop() < 10) show_header.play()
          }
        }
        else {
          show_header.reverse()
        }
      }
    })

    if (has_filters) {
      const show_filters = gsap.to('.collection--filters',
        {
          paused: true,
          yPercent: -100,
          duration: 0.5,
          ease: 'none'
          //ease: Easings.header,
        })

      let top = filters.offset().top
      ScrollTrigger.create({
        start: "top " + top,
        end: 99999,
        onUpdate: (self) => {
          if (window.innerWidth < 1023) return
          let scroll_top = $(window).scrollTop()
          if (scroll_top < $('.section--products > .container').offset().top) {
            show_filters.reverse()
            $('.collection--filters').removeClass('is-pinned')
            return
          }
          else {
            $('.collection--filters').addClass('is-pinned')
          }
          if (scroll_top < top && self.direction === -1) {
            show_filters.play()
            $('.collection--filters').removeClass('is-pinned')
            return
          }
          else if (self.direction === -1) {
            $('.collection--filters').addClass('is-pinned')
            show_filters.reverse()
          }
          else {
            show_filters.play()
          }
        }
      })
    }

  }, delay)
}

