//import ScrollTrigger from "gsap/ScrollTrigger"

export default function () {
  const selector = ".caleidoscopio--clients"
  if (gsap.utils.toArray(selector).length == 0) return
  gsap.to(".page", {
    scrollTrigger:
    {
      trigger: selector,
      start: "top 50%",
      end: "+=50",
      scrub: 1, // smooth scrubbing, takes 1 second to "catch up" to the scrollbar
      snap: {
        snapTo: "labels",
        delay: 0.2,
        ease: "power1.inOut"
      },
    },
    backgroundColor: '#000000',
  })

  gsap.to(".page", {
    scrollTrigger:
    {
      trigger: selector,
      start: "top 70%",
      end: "100%",
      toggleClass: {
        targets: ".page", className: "blackened"
      }
    },
  })

}