import ScrollTrigger from "gsap/ScrollTrigger"
import Easings from './easings'

export default function () {
  const el = document.querySelector(".collection--filters")
  /*
  const observer = new IntersectionObserver(
    ([e]) => e.target.classList.toggle("is-pinned", e.intersectionRatio < 1),
    { threshold: [1] }
  );
  observer.observe(el);
  */

  const filters = '.collection--filters--content'
  gsap.set(filters, { duration: 0, height: 0, overflowY: 'hidden', overflowX: 'hidden' })
  var filters_timeline = gsap
    .timeline({ paused: true })
    .to(
      filters,
      {
        height: 'auto',
        duration: 0.75,
        ease: 'ease-in-out'//Easings.header,
      })
  $('.collection--filters--icon').on('click', function () {
    if ($('html,body').hasClass('filters-open')) {
      filters_timeline.reverse().play().reverse()
    }
    else {
      filters_timeline.play()
    }
    $('html,body').toggleClass('filters-open')
  })

  ScrollTrigger.create({
    onUpdate: () => {
      filters_timeline.reverse().play().reverse()
      $('html,body').removeClass('filters-open')
    }
  })
}