export default function () {

  gsap.utils.toArray('.mosaic').forEach(m => {
    gsap.to(m, { opacity: 0, duration: 0 })
    m.setAttribute('fade', true)
    m.setAttribute('fire', true)
  })

  // grid (multiple rows)
  gsap.utils.toArray('.mosaic--item').forEach(card => {
    gsap.to(card, {
      duration: 1,
      scrollTrigger: {
        toggleClass: "visible",
        start: "top 70%",
        trigger: card,
        scrub: true,
        duration: {
          min: 1,
        }
      },
    })
  })
  gsap.to(".mosaic .col-6:nth-child(odd)", {
    y: -150,
    ease: "none",
    scrollTrigger: {
      trigger: ".mosaic",
      scrub: true
    },
  })
  gsap.to(".mosaic .col-6:nth-child(even)", {
    y: 150,
    ease: "none",
    scrollTrigger: {
      trigger: ".mosaic",
      scrub: true
    },
  })
}