//import ScrollTrigger from "gsap/ScrollTrigger"

export default function () {
  const accordion_items = gsap.utils.toArray('[accordion-content]')
  const duration = 0.4
  const easing = 'power2'
  accordion_items.forEach(item => {
    gsap.to(item, { height: 0, overflow: 'hidden', duration: 0 })
  })
  $('.accordion--heading').on('click', function () {
    let target = $(this).attr('accordion-toggle'),
      selector = '[accordion-content="' + target + '"]',
      element = $(selector)
    if ($(this).hasClass('active')) {
      $(this).removeClass('active')
      gsap.to(
        selector,
        { height: 0, duration: duration, ease: easing }
      )
      return
    }
    $(this).addClass('active')
    element.addClass('active')
    gsap.to(
      selector,
      { height: 'auto', duration: duration, ease: easing }
    )
  })

  $('[finiture-toggle]').on('click', function () {
    if ($(this).hasClass('active')) {
      return
    }
    let finiture = $(this).closest('.finiture--variants')
    finiture.find('[finiture-toggle].active').removeClass('active')
    finiture.find('[finiture-content].active').hide(0)
    $(this).addClass('active')
    let target = $(this).attr('finiture-toggle'),
      selector = '[finiture-content="' + target + '"]',
      element = finiture.find(selector)

    element.addClass('active')
    element.show(0)
  })
  $('[finiture-toggle]:first-child').trigger('click')
}