export default function () {
  const clients_caleidoscopio = gsap.utils.toArray('.page--caleidoscopio .caleidoscopio--clients--item')

  clients_caleidoscopio.forEach((item, i) => {
    let right, fromLeft
    if (i == 0 | i % 4 == 0) {
      fromLeft = 0
      right = 0
    }
    if (i == 1 | i % 4 == 1) {
      fromLeft = -100
      right = 50
    }
    if (i == 2 | i % 4 == 2) {
      fromLeft = 0
      right = 80
    }
    if (i == 3 | i % 4 == 3) {
      fromLeft = -100
      right = 160
    }
    gsap.fromTo(item,
      {
        xPercent: fromLeft
      },
      {
        ease: 'linear',//'power1.inOut',
        scrollTrigger: {
          trigger: item,
          scrub: true,
          //once: true,
          //duration: { min: 1.5, max: 2 },
          start: "top 50%",
          end: "+=250",
        },
        xPercent: right,
        //duration: 1,
        delay: i,
      })
  })

  const clients_brand_platform = gsap.utils.toArray('.page--brand-platform .caleidoscopio--clients--item'),
    trigger_brand_platform = '.page--brand-platform .caleidoscopio--clients'

  clients_brand_platform.forEach((item, i) => {
    let from, to

    if (i == 0) {
      from = { left: 0 }
      to = { left: 0 }
    }
    if (i == 1) {
      from = { left: 0 }
      to = { left: '100%', xPercent: -100 }
    }
    if (i == 2) {
      from = { left: 0 }
      to = { left: 0, right: 0 }
    }
    if (i == 3) {
      from = { left: 0 }
      to = { left: '80%', xPercent: -100 }
    }
    if (i == 4) {
      from = { left: 0 }
      to = { left: '20%', right: 0 }
    }
    if (i == 5) {
      from = { left: 0 }
      to = { left: '100%', xPercent: -100 }
    }
    if (i == 6) {
      from = { left: 0 }
      to = { left: 0 }
    }
    if (i == 7) {
      from = { left: 0 }
      to = { left: '90%', xPercent: -100 }
    }
    if (i == 8) {
      from = { left: 0 }
      to = { left: '20%', right: 0 }
    }
    if (i == 9) {
      from = { left: 0 }
      to = { left: '100%', xPercent: -100 }
    }
    gsap.fromTo(item,
      from,
      {
        ...to,
        ease: 'linear',
        scrollTrigger: {
          trigger: item,//clients_brand_platform,
          scrub: true,
          start: "top 80%",//"top 50%",
          end: "top 70%",//"+=250",
        },
        delay: i,
      })

  })

}