import ScrollTrigger from "gsap/ScrollTrigger"

gsap.registerPlugin(ScrollTrigger)

export default function () {
  // footer
  const footer_links = gsap.utils.toArray('[footer-animate-link]')
  footer_links.forEach((box, i) => {
    let y = 100
    let x = 0
    const anim_delay = gsap.fromTo(
      box,
      {
        autoAlpha: 0,
        y: y,
        x: x,
      },
      {
        duration: 1,
        autoAlpha: 1,
        y: 0,
        x: 0,
        delay: i / 10
      }
    )
    ScrollTrigger.create({
      trigger: box,
      animation: anim_delay,
      toggleActions: 'play none none none',
      once: true,
    })
  })

  const boxes = gsap.utils.toArray('[footer-animate]')
  boxes.forEach((box, i) => {
    let y = 0
    let x = 0
    let delay = 0
    if (box.hasAttribute('left')) x = -100
    if (box.hasAttribute('right')) x = 100
    if (box.hasAttribute('up')) y = 100
    if (box.hasAttribute('down')) y = -100
    if (box.hasAttribute('delay')) delay = i / 10
    const anim = gsap.fromTo(
      box,
      {
        autoAlpha: 0,
        y: y,
        x: x,
      },
      {
        duration: 1,
        autoAlpha: 1,
        y: 0,
        x: 0,
        delay: delay,
      }
    )
    ScrollTrigger.create({
      trigger: box,
      animation: anim,
      toggleActions: 'play none none none',
      once: true,
    })
  })
}