export default function () {
  gsap.set('.menu--wrapper', { display: 'none', opacity: 0, duration: 0 })
  var tl = gsap.timeline({
    paused: true,
    ease: 'power2.inOut',
  }),
    tx = tl
      .to('.header--language', { duration: 0.15, top: 0, opacity: 0, y: -50 })
      .to('.header--search', { duration: 0.15, top: 0, opacity: 0, y: -50 })
      .to('.menu--wrapper', { duration: 0, display: 'block' })
      .to('.menu--wrapper', { duration: 0.25, opacity: 1 })

  var menu_timeline = gsap.timeline({
    paused: true
  })
  const menu_items = [
    ...gsap.utils.toArray('.menu .menu--content'),
    ...gsap.utils.toArray('.menu .menu--primary--item'),
    ...gsap.utils.toArray('.menu .menu--secondary--item'),
    ...gsap.utils.toArray('.menu .menu--language--item'),
    ...gsap.utils.toArray('.menu .menu--social--item')
  ]
  menu_items.forEach((item, i) => {
    menu_timeline.fromTo(
      item,
      {
        opacity: 0,
        y: -20,
      },
      {
        duration: 0.08,
        opacity: 1,
        y: 0,
        delay: i == 1 ? 0.5 : 0
      }
    )
  })

  $('.header .menu--icon').on('click', function () {
    var menu = $(this),
      body = $('body,html')

    if (menu.hasClass('open')) {
      menu.removeClass('open')
      body.removeClass('menu-open')
      menu_timeline
        .reverse()
        .play()
        .reverse()
        .then(() => {
          tx.reverse()
            .play()
            .reverse()
        })
      return
    }

    menu.addClass('open')
    body.addClass('menu-open')
    tx
      .play()
      .then(() => {
        menu_timeline
          .play()

      })
  })

  $('.header .dropdown--wrapper').on({
    click: function () {
      $(this).toggleClass('open')
      $(this).find('.dropdown--items').stop().toggle(200)
    },
    mouseenter: function () {
      $(this).addClass('open')
      $(this).find('.dropdown--items').stop().show(200)
    },
    mouseleave: function () {
      $(this).removeClass('open')
      $(this).find('.dropdown--items').stop().hide(200)
    },
  })

  const search = gsap.utils.toArray('.search')
  gsap.to(search, { height: 0, duration: 0 })
  const search_timeline = gsap.timeline({
    paused: true,
    ease: 'power2.inOut',
  })
  search_timeline.fromTo(
    search,
    {
      height: 0
    },
    {
      height: 'auto'
    }
  )

  $('.header--search, .search--toggle').on('click', function () {
    var search = $('.search')
    if (search.hasClass('open')) {
      search.removeClass('open')
      search_timeline
        .reverse()
        .play()
        .reverse()
        .then(() => {
          search_timeline.reverse()
            .play()
            .reverse()
        })
      return
    }

    search.addClass('open')
    search_timeline
      .play()
      .then(() => {
        search_timeline
          .play()

      })
  })

}