export default function () {

  // section-1
  const storia_images = gsap.utils.toArray('.page--storia .text-image img')

  storia_images.forEach((item, i) => {
    gsap.fromTo(item,
      {
        yPercent: 25,
      },
      {
        ease: 'linear',//'power1.inOut',
        scrollTrigger: {
          trigger: item,
          scrub: true,
          //once: true,
          //duration: { min: 1.5, max: 2 },
          //start: "top 50%",
          //end: "+=250",
        },
        yPercent: -15,//-25,
        //duration: 1,
        delay: i,
      })
  })


}