export default function () {
  const modal_items = gsap.utils.toArray('[modal]')
  modal_items.forEach(item => {
    gsap.set(item, { display: 'none', duration: 0 })
    $('[modal-toggle]').on('click', function () {
      let target = $(this).attr('modal-toggle'),
        selector = '[modal="' + target + '"]'
      gsap.fromTo(
        selector,
        { display: 'block', height: 0, opacity: 0, overflow: 'hidden', duration: 0, pointerEvents: 'all' },
        { height: '100vh', opacity: 1, duration: 0.5 },
      )
      gsap.to(
        'body,html',
        {
          overflow: 'hidden',
          duration: 0
        }
      )
    })
    $('.modal--close').on('click', function () {
      let target = $(this).closest('[modal]').attr('modal'),
        selector = '[modal="' + target + '"]'
      gsap.to(
        selector,
        { display: 'block', height: 0, opacity: 0, overflow: 'hidden', duration: 0.5, pointerEvents: 'none' },
      )
      gsap.to(
        'body,html',
        {
          overflow: 'auto',
          duration: 0
        }
      )
    })
  })
}
