import Vimeo from '@vimeo/player'

export default function () {
  const iframes = document.querySelectorAll('iframe.vimeo')
  if (iframes == null) return
  iframes.forEach(iframe => {
    const player = new Vimeo(iframe)
    const play = $(iframe).parent().find('.play')
    const pause = $(iframe).parent().find('.pause')
    play.on('click', () => { player.play(); window.console.log('TEST') })
    pause.on('click', () => { player.pause() })
  })
}