import Swiper from 'swiper/bundle'
import { ScrollTrigger } from "gsap/ScrollTrigger"
import Split from './split'
import Fade from './fade'
import Mosaic from './mosaic.js'
import SliderSide from './slider-side'
import Timeline from './timeline'
import Tabs from './tabs'
import Accordion from './accordion'
import Modal from './modal'
import Filters from './filters'
import Menu from './menu'
import Footer from './site-footer'
import Header from './site-header'
import Toggle from './toggle'
import Vimeo from './vimeo'
import Caleidoscopio from './caleidoscopio'
import Clients from './clients'
import Crew from './crew'
import HomeHero from './home-hero'
import BrandPlatform from './brand-platform'
import BrandStoria from './brand-storia'

export default class Main {

  constructor() {
    this.init();
  }

  init() {

    gsap.registerPlugin(ScrollTrigger)

    SliderSide()
    Mosaic()
    Timeline()
    Tabs()
    Accordion()
    Modal()
    Filters()
    Menu()
    Footer()
    Toggle()
    Vimeo()
    Caleidoscopio()
    Clients()
    Crew()
    HomeHero()
    BrandPlatform()
    BrandStoria()

    // hero text
    /*
    $('.section--hero--plus--icon').on('click', function (t) {
      $(this).closest('.section--hero--plus--content').toggleClass('show')
    })
    */
    const toggle = gsap.utils.toArray('.section--hero--plus--content .full')
    toggle.forEach(item => {
      gsap.to(item, { height: 0, overflow: 'hidden', duration: 0 })
      $(item).closest('.section--hero--plus--content').find('.section--hero--plus--icon').on('click', function () {
        if ($(item).hasClass('show')) {
          gsap.to(
            item,
            { height: 0, duration: 0.5 }
          )
        }
        else {
          gsap.to(
            item,
            { height: 'auto', duration: 0.5 }
          )
        }
        $(item).toggleClass('show')
      })
    })

    // swiper 
    var swiper_default = new Swiper(".composizione--slider-full .swiper--default", {
      navigation: {
        nextEl: ".composizione--slider-full .swiper--default--next",
        prevEl: ".composizione--slider-full .swiper--default--prev",
      },
      effect: 'fade',//'flip'
      fadeEffect: {
        crossFade: true
      },
      speed: 1000,
      loop: true,
    })

    var swiper_multiple = new Swiper(".section--multiple-slider .swiper--multiple", {
      navigation: {
        nextEl: ".section--multiple-slider--next",
        prevEl: ".section--multiple-slider--prev",
      },
      /*
      effect: 'fade',//'flip'
      fadeEffect: {
        crossFade: true
      },
      */
      speed: 1000,
      loop: true,
      slidesPerView: "auto",
      spaceBetween: 30,
    })

    var swiper_multiple = new Swiper(".crew--designers--slider", {
      navigation: {
        nextEl: ".crew--designers--next",
        prevEl: ".crew--designers--prev",
      },
      speed: 1000,
      slidesPerView: "auto",
    })

    const preview_slider = new Swiper(".section--preview-products", {
      navigation: {
        nextEl: ".section--preview-products .swiper--default--next",
        prevEl: ".section--preview-products .swiper--default--prev",
      },
      speed: 1000,
      //loop: true,
      //autoHeight: true,
      breakpoints: {
        1024: {
          slidesPerView: 2,
        },
        1140: {
          slidesPerView: 3,
        },
        1600: {
          slidesPerView: 4,
        }
      }
    })

    // preloader
    function animate_page() {
      const
        development_mode = false,//true,
        delay_header = development_mode ? 0 : 2000,
        delay_split = development_mode ? 0 : 2000,
        delay_fade = development_mode ? 0 : 2000
      page_timeline
        .call(Header(delay_header))
        .call(Split(delay_split))
        .call(Fade(delay_fade))
        .play()
    }

    const preloader = document.getElementsByClassName('.preloader')
    const page_timeline = gsap.timeline({
      paused: true,
    })

    if (preloader !== null) {
      gsap.set('body,html', { overflow: 'hidden', duration: 0 })
      var prelaoder_timeline = gsap.timeline({
        paused: true,
        ease: 'linear'
      })
      window.addEventListener('DOMContentLoaded', (event) => {
        setTimeout(() => {
          var tx = prelaoder_timeline
            .to('.preloader svg', {
              display: 'none',
              duration: 1,
              scale: 100,
              opacity: 0,
            })
            .to('.preloader', {
              opacity: 0,
              display: 'none',
              duration: 0.5
            })
          tx.play()
            .then(() => {
              $('html,body').removeAttr('style')
            })
          animate_page()
        }, 2000)
      })
    }
    else {
      $('html,body').removeAttr('style')
      animate_page()
    }
  }
}

const main = new Main();
