import { SplitText } from "../vendors/SplitText.js"
import Easings from './easings'

const split = document.querySelectorAll("[split]")
gsap.set(split, { opacity: 0, duration: 0 })

export default function (delay = 0) {
  split.forEach(s => {
    new SplitText(s, {
      charClass: "st--char",
      wordClass: "st--word",
      lineClass: "st--line",
      globalClass: "st--wrapper",
      emptySpaceName: "st--space",
    })
    setTimeout(() => {
      gsap.set(split, { opacity: 1, duration: 0 })
      gsap.utils.toArray(s.children).forEach((line, i) => {
        gsap.set(line, { overflow: 'hidden' })
        gsap.utils.toArray(line.children).forEach((word, index) => {
          gsap.fromTo(word, {
            yPercent: 100,
            //ease: Easings.split
          },
            {
              yPercent: 0,
              duration: 1,
              delay: i / 10,
              ease: Easings.split,
            })
        })
      })
    }, delay)
  })
}