import Swiper from 'swiper/bundle'

export default function () {

  //const timeline = document.querySelectorAll('.timeline')

  //if (timeline.length == 0) return
  document.querySelectorAll('.slider-side').forEach(s => {
    if ($(s).find('.swiper-slide').length == 1) {
      $(s).find('.slider-side--nav').remove()
    }
    else {
      $(s).addClass('slider-side-ok')
    }
  })

  const swiper_side = new Swiper(".slider-side-ok", {
    navigation: {
      nextEl: ".slider-side--next",
      prevEl: ".slider-side--prev",
    },
    //effect: 'fade',//'flip'
    fadeEffect: {
      crossFade: true
    },
    speed: 1000,
    loop: true,
    //autoHeight: true,
    autoHeight: true,
    breakpoints: {
      1024: {
        autoHeight: false,
      }
    }
  })
}