export default function () {
  if ($('.homepage--hero').length == 0) return


  const hero_image = '.homepage--hero--image'
  gsap.fromTo(hero_image,
    {
      yPercent: 0,
    },
    {
      yPercent: -40,
      scrollTrigger:
      {
        trigger: hero_image,
        //start: "top bottom",
        //end: "bottom center",
        scrub: 1,
      },
    }
  )


  const foreground = '.homepage--timeless--image--foreground'
  const background = '.homepage--timeless--image--background'
  const foreground_image = '.homepage--timeless--image--foreground img'
  const background_width = gsap.getProperty(background, "width", "px")
  gsap.set(foreground_image, { width: background_width, duration: 0 })
  gsap.fromTo(foreground,
    {
      width: "0%",
    },
    {
      width: "100%",
      scrollTrigger:
      {
        trigger: foreground,
        start: "top 75%",
        end: "bottom center",
        scrub: 1,
      },
    }
  )
  gsap.fromTo(background,
    {
      opacity: 0.1,
    },
    {
      opacity: 1,
      scrollTrigger:
      {
        trigger: foreground,
        start: "top bottom",
        end: "bottom center",
        scrub: 1,
      },
    }
  )

  const vertical = '.timeline--vertical'
  gsap.fromTo(vertical,
    {
      y: 100,
    },
    {
      y: -100,
      scrollTrigger:
      {
        trigger: '.timeline',
        //start: "top bottom",
        //end: "bottom center",
        scrub: 1,
      },
    }
  )

  const vertical_text = '.homepage--text-image--vertical'
  gsap.fromTo(vertical_text,
    {
      y: 100,
    },
    {
      y: 200,
      scrollTrigger:
      {
        trigger: '.homepage--text-image',
        //start: "top bottom",
        //end: "bottom center",
        scrub: 1,
      },
    }
  )

  const dots_foreground = '.homepage--dots--foreground'
  const dots_exclusion = '.homepage--dots--exclusion'
  gsap.fromTo(dots_foreground,
    {
      opacity: 1,
    },
    {
      opacity: 0,
      scrollTrigger:
      {
        trigger: dots_foreground,
        start: "top 75%",
        end: "top 10%",
        scrub: 1,
      },
    }
  )
  gsap.fromTo(dots_exclusion,
    {
      yPercent: 0,
    },
    {
      yPercent: -100,
      scrollTrigger:
      {
        trigger: dots_foreground,
        start: "top top",
        end: "top -35%",
        //end: "top -5%",
        scrub: 1,
      },
    }
  )
  gsap.fromTo('.homepage--dots--white',
    {
      opacity: 1,
      zIndex: 2,
    },
    {
      opacity: 0,
      zIndex: 1,
      scrollTrigger:
      {
        trigger: '.homepage--dots--white',
        start: "bottom top",
        end: "top -1%",
        scrub: 1,
      },
    }
  )

  const dots_white_foreground = '.homepage--dots--white--foreground'
  const dots_white_exclusion = '.homepage--dots--white--exclusion'
  gsap.fromTo(dots_white_foreground,
    {
      opacity: 1,
    },
    {
      opacity: 0,
      scrollTrigger:
      {
        trigger: dots_white_foreground,
        start: "top 75%",
        end: "top 10%",
        scrub: 1,
      },
    }
  )
  gsap.fromTo(dots_white_exclusion,
    {
      yPercent: 0,
    },
    {
      yPercent: -100,
      scrollTrigger:
      {
        trigger: dots_white_foreground,
        start: "top top",
        end: "top -35%",
        //end: "top -5%",
        scrub: 1,
      },
    }
  )

  // additional
  gsap.fromTo(dots_white_foreground,
    {
      yPercent: 0,
    },
    {
      yPercent: -100,
      scrollTrigger:
      {
        trigger: dots_white_foreground,
        start: "top top",
        end: "top -35%",
        //end: "top -5%",
        scrub: 1,
      },
    }
  )

  const parallax = '.homepage--parallax--backdrop--image'
  gsap.fromTo(parallax,
    {
      yPercent: -40,
    },
    {
      yPercent: 0,
      scrollTrigger:
      {
        trigger: parallax,
        start: "top bottom",
        end: "bottom -100%",
        scrub: 1,
      },
    }
  )

}