gsap.utils.toArray('[data-content]').forEach(card => {
  gsap.to(card, { height: 0, overflow: 'hidden', duration: 0 })
})

const duration = 0.35

export default function () {
  //$('[data-content]').stop().hide(0)
  $('[data-toggle]').on({
    click: function () {
      let selector = '[data-content="' + $(this).attr('data-toggle') + '"]'
      if (!$(this).hasClass('open')) {
        gsap.to(
          selector,
          {
            height: 'auto',
            duration: duration,
          }
        )
        if ($(this).parents('.menu').length) {
          gsap.to(
            '.menu--social',
            {
              opacity: 0,
              duration: 0.5,
            }
          )
        }
      } else {
        gsap.to(
          selector,
          {
            height: 0,
            duration: duration,
          }
        )
        if ($(this).parents('.menu').length) {
          gsap.to(
            '.menu--social',
            {
              opacity: 1,
              duration: 0.5,
            }
          )
        }
      }
      $(this).toggleClass('open')
    },
  })
}