export default function () {
  gsap.utils.toArray(".crew--half--item").forEach(c => {
    gsap.to(c, { opacity: 0, duration: 0 })
    gsap.to(c, {
      duration: 1,
      opacity: 1,
      scrollTrigger: {
        start: "top 70%",
        end: "+=50%",
        trigger: c,
        scrub: true
      },
    })
    gsap.to(c, {
      scrollTrigger: {
        toggleClass: "visible",
        start: "top 50%",
        trigger: c,
      },
    })
  })
}